<template>
  <div>
    <div class="search">
      <input ref="input" type="text" v-model="keyword" class="search-input" placeholder="请输入城市名或拼音" >
    </div>
    <div class="search-content" ref="search" v-show="keyword">
      <ul>
        <li class="search-item border-bottom" v-for="item of list" :key="item.id" @click="handleCityClick(item.name)">
      {{item.name}}
    </li>
        <li class="search-item border-bottom text-center" v-show="hasNoData">没有找到匹配数据!</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default{
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  computed: {
    hasNoData () {
      return !this.list.length
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$refs.input.value = ''
      this.list = []
      this.keyword = ''
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.search)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles'
  .search
    height .72rem
    background-color $bgColor
    padding 0 .1rem
    .search-input
      background-color #fff
      width 100%
      height .62rem
      padding 0 .1rem
      line-height .62rem
      text-align center
      border-radius .06rem
      color #666
      box-sizing border-box
  .search-content
    overflow hidden
    position absolute
    z-index 1
    top 1.58rem
    left 0
    right 0
    bottom 0
    background-color #eee
    .search-item
      line-height .62rem
      padding-left .2rem
      color #666
      background-color #fff
    .text-center
      text-align center
</style>



